<template>
  <div class="login-container">
    <div class="login">
      <div class="login-pannel">
        <div class="header">
          <span class="login-title">用户绑定</span>
        </div>
        <div class="login-form">
          <div class="fieldset" v-if="formShow">
            <el-form ref="form" :model="form" :rules="rules" v-loading="formLoading">
              <el-form-item style="margin-bottom:2.5px;" prop="user">
                <el-input
                  :disabled="true"
                  class="login-input"
                  v-model="form.user"
                  :class="{'loginActive':active}"
                  placeholder="请输入用户名"
                  clearable
                  @focus="active = true"
                  @keyup.native="keyNext($event,'loginPwd')"
                  autofocus="autofocus"
                >
                  <template slot="prepend">
                    <i class="iconfont icon-user"></i>
                  </template>
                </el-input>
              </el-form-item>
              <el-form-item prop="pwd">
                <el-input
                  class="login-input"
                  id="loginPwd"
                  style="margin-bottom:-8px;border:none"
                  v-model="form.pwd"
                  :type="password"
                  :class="{'loginActive':!active}"
                  placeholder="请输入密码"
                  @focus="active = false"
                  @keyup.native="keyNext($event,'loginCheckPwd')"
                >
                  <template slot="prepend">
                    <i class="iconfont icon-weibiaoti1"></i>
                  </template>
                  <template slot="suffix">
                    <i
                      class="iconfont"
                      :class="{'icon-biyanjing': password == 'password','icon-17yanjing2': password == 'text'}"
                      @click="seePwd"
                    ></i>
                  </template>
                </el-input>
              </el-form-item>
              <el-form-item prop="checkpwd">
                <el-input
                  class="login-input"
                  id="loginCheckPwd"
                  style="margin-bottom:-8px;border:none"
                  v-model="form.checkpwd"
                  :type="password"
                  :class="{'loginActive':!active}"
                  placeholder="请输入确认密码"
                  @focus="active = false"
                  @keyup.native="keyEnter($event)"
                >
                  <template slot="prepend">
                    <i class="iconfont icon-weibiaoti1"></i>
                  </template>
                  <template slot="suffix">
                    <i
                      class="iconfont"
                      :class="{'icon-biyanjing': checkpassword == 'password','icon-17yanjing2': checkpassword == 'text'}"
                      @click="seeCheckPwd"
                    ></i>
                  </template>
                </el-input>
              </el-form-item> 
              <el-form-item prop="bingbtn">
                <el-button style="width:285px"
                  type="primary"
                  class="login-form-btn"
                  @click="binding"
                  v-if="formShow"
                  :loading="loading"
                >绑定</el-button>  
              </el-form-item>
            </el-form> 
          </div>  
        </div>
      </div>
    </div>
    <div class="footer">闽ICP18002063号-1睿和科技-Copyright &nbsp;&copy; &nbsp;2018 &nbsp;睿和科技</div>
  </div>
</template>
<script>
import api from '../utils/api'
import http from '../utils/http'
import md5 from 'js-md5'
import cookie from '../utils/cookieOperate.js'
export default {
  data () {
    return {
      formLoading: false,
      active: true,
      thirdid:'',
      form: {
        user: '',
        pwd: '',
        checkpwd:'',
      },
      formShow: true,
      loading: false,
      password: 'password',
      checkpassword:'password',
      rules: {
        user: [
          {
            required: true,
            message: '请输入用户名',
            trigger: 'blur'
          },
          {
            min: 2,
            max: 25,
            message: '用户名长度在 2 到 25 个字符',
            trigger: 'blur'
          }
        ],
        pwd: [
          {
            required: true,
            message: '请输入密码',
            trigger: 'blur'
          },
          {
            min: 6,
            max: 32,
            message: '密码长度在 6 到 32 个字符',
            trigger: 'blur'
          }
        ],
        checkpwd: [
          {
            required: true,
            message: '请输入确认密码',
            trigger: 'blur'
          },
          {
            min: 6,
            max: 32,
            message: '确认密码长度在 6 到 32 个字符',
            trigger: 'blur'
          }
        ]
      }
    }
  },
  mounted () {
    // 一个参数
    let params = this.$route.params.id.split("&");
    let firstParams = params[0];
    this.thirdid = params[0].split("=")[1];
    this.form.user = this.thirdid;
    //let secondParams = params[1]; 
    console.log(firstParams)
    console.log(secondParams)
  },
  methods: {
    binding () {
      this.$refs['form'].validate(valid => {
        if (valid) {
        if(this.form.pwd != this.form.checkpwd) {
            this.$message({
                message: '确认密码和密码输入不一致',
                type: 'warning'
            })
            return;
        }
          this.loading = true
          this.bindingAjax(this.form.user, md5(this.form.pwd))
        }
      })
    }, 
    seePwd () {
      if (this.password == 'text') {
        this.password = 'password'
      } else {
        this.password = 'text'
      }
    },
    seeCheckPwd () {
      if (this.checkpassword == 'text') {
        this.checkpassword = 'password'
      } else {
        this.checkpassword = 'text'
      }
    },
    keyNext (ev, id) {
      if (ev.keyCode == 13) {
        document.getElementById(id).focus()
      }
    },
    keyEnter (ev) {
      if (ev.keyCode == 13) {
        this.binding()
      }
    },
    async loginAjax (user, psd) {
      let res = await http.post(api.login, {
        name: user,
        pwd: psd,
        from: '11'
      })
      this.loading = false
      if (res.type) {
        this.$message({
          showClose: true,
          message: res.message,
          type: res.type
        })
      } else {
        cookie.set(api.cookie.user_infor, JSON.stringify(res.UserInfo))
        cookie.set(api.cookie.token, res.Data)
        this.formLoading = false
        if (this.$route.query.relogin) {
          let tempPath = window.sessionStorage.getItem('last_path')
          if (tempPath && tempPath.indexOf('login') < 0) {
            window.location.href = tempPath
          } else {
            this.$router.push('/main/teacherSpace/teacherView/0')
          }
        } else {
          this.$router.push('/main/teacherSpace/teacherView/0')
        }
      }
    },
    async bindingAjax (user, psd) {
      let res = await http.post(api.binding, {
        id:'',
        name: user,
        pwd: psd,
        usertype:'1',
        thirdid:this.thirdid
      })
      this.loading = false
      if (res.type) {
        this.$message({
          showClose: true,
          message: res.message,
          type: res.type
        })
      } else {
         this.loginAjax(user, psd);
      }
    }
  }
}
</script>
<style lang='less'>
.login-container {
  position: absolute;
  top: 120px;
  left: 0;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 100%;
  height: calc(100% - 120px);
  user-select: none;
}

.footer,
.header {
  display: -webkit-box;
  display: flex;
  align-items: center;
}
.loginActive .iconfont {
  color: #000 !important;
}
.login {
  position: relative;
  width: 100%;
  height: 700px;
  background-image: url('../assets/login_bg.png');
  background-size: cover;
  background-repeat: no-repeat;
}
/*=========表单开始==========*/

.login-pannel {
  position: absolute;
  top: 25%;
  right: 22%;
  height: 380px;
  width: 360px;
  background: #fff;
}

.header {
  justify-content: space-between;
  height: 60px;
  width: 100%;
  padding-left: 36px;
  text-align: left;
  .login-title {
    font-size: 1.1rem;
    font-weight: normal;
    align-self: flex-end;
  }
  .btn-scan {
    height: 60px;
    width: 66px;
    background-image: url("../assets/scan-2.png");
    &:hover {
      cursor: pointer;
    }
  }
  .btn-form {
    background-image: url("../assets/login-form.png") !important;
  }
}
.login-form {
  height: 300px;
  width: 100%;
  padding: 0 36px 0;
}
.er-code {
  width: 100%;
  text-align: center;
  margin-top: 25px;
}
.code-img-box {
  width: 166px;
  height: 166px;
  margin: 0 auto;
  margin-bottom: 18px;
  border: 1px solid #aaa;
}
.code-img-box img {
  width: 100%;
  height: 100%;
  display: inline-block;
}
.er-code-sub {
  width: 100%;
  height: 40px;
  display: flex;
  justify-content: space-between;
  margin-top: 18px;
}
.er-code-item {
  width: 33.33%;
  height: 100%;
  line-height: 40px;
  color: #aaa;
}
.er-code-item .iconfont {
  font-size: 1.25rem;
}
.fieldset {
  width: 100%;
  height: 150px;
  margin-top: 36px;
  .el-form-item {
    margin-top: 28px;
    .el-form-item__error {
      top: 108% !important;
    }
  }
  .login-input {
    .iconfont {
      font-size: 1.5rem !important;
    }
    .el-input__suffix {
      line-height: 40px;
    }
  }
}
.login-form-btn {
  width: 290px;
  height: 46px;
  margin-top: -8px;
  border: none;
  border-radius: 0 !important;
  font-size: 24px !important;
  color: #fff;
} 
.forget-btn {
  width: 100%;
  margin-top: 15px;
  span {
    float: right;
    font-size: 0.8rem;
    color: #aaa;
    &:hover {
      color: #409eff;
      cursor: pointer;
    }
  }
}
/*=========表单结束==========*/

.footer {
  justify-content: center;
  height: calc(100% - 700px);
  width: 100%;
  font-size: 14px;
  background-color: #fff;
  color: #666;
}
.login-input .el-input-group__append {
  background: none !important;
  border: none !important;
  padding: 0 !important;
}
.login-input .el-input-group__append .iconfont {
  font-size: 1.25rem !important;
}
</style>


